let seq  = async (arr) => {
    for(let a of arr){
        await a()
    }
}

import {test} from 'ava'

test(async t => {
    let a = () => new Promise(resolve=> {setTimeout(()=>{ resolve('a')}, 500)})
    let b = () => new Promise(resolve=> {setTimeout(()=>{ resolve('b')}, 500)})
    let c = () => new Promise(resolve=> {setTimeout(()=>{ resolve('c')}, 500)})

    t.deepEqual(await seq([a, b, c]), ['a', 'b', 'c'])

})